import { shuffleArray } from './utils';

export type Question = {
  // category: string;
  correct_answer: string;
  correct_answer_ar: string;
  incorrect_answers: string[];
  incorrect_answers_ar: string[];
  question: string;
  question_ar: string;
  question_image: string;
  // type: string;
};

// export enum Difficulty {
//   EASY = "easy",
//   MEDIUM = "medium",
//   HARD = "hard",
// }

export type QuestionsState = Question & { answers: string[] }
       & { answers_ar: string[] };
       

const random = Math.random() - 0.5;
export const fetchQuizQuestions = async () => {
  // export const fetchQuizQuestions = async (amount: number): Promise<QuestionsState[]> => {
    
  // const endpoint = `https://opentdb.com/api.php?amount=5`;
  // const endpoint = `https://polls.apiblueprint.org/questions`;
  const endpoint = `https://private-a03d34-shamasil.apiary-mock.com/questions`;
  const data = await (await fetch(endpoint)).json();
  console.log(data);
  return data.results.map((question: Question) => ({
    ...question,
    answers: shuffleArray(
      [...question.incorrect_answers, question.correct_answer], [random]
      ),
    answers_ar: shuffleArray(
      [...question.incorrect_answers_ar, question.correct_answer_ar], [random]
      )
  }))
};
